Office.onReady((info) => {
  if (info.host === Office.HostType.Word) {
    // Load voices when the add-in is ready
    loadVoices();

    // Set up the button click event for sending text to TTS
    document.getElementById('sendTextButton').onclick = sendTextToTTS;

    // Assuming you want to hide some initial message and show the app body
    // Ensure your HTML has elements that match these IDs or remove these lines if not needed.
    // document.getElementById("sideload-msg").style.display = "none"; // Remove if not in HTML
    // document.getElementById("app-body").style.display = "flex"; // Remove if not in HTML
  }
});

const audio = document.getElementById('audioSource');
const volumeControl = document.getElementById('volumeControl');

volumeControl.addEventListener('input', () => {
  audio.volume = volumeControl.value;
});


function loadVoices() {
  // Get the user-input IP address and port number
  const ipAddress = document.getElementById('serverIP').value;
  const portNumber = document.getElementById('portNumber').value;
  const url = `http://${ipAddress}:${portNumber}/api/voices`;

  fetch(url)
    .then(response => response.json())
    .then(data => {
        const selectElement = document.getElementById('voice');
        data.voices.forEach(voice => {
            const option = document.createElement('option');
            option.value = voice;
            option.textContent = voice;
            selectElement.appendChild(option);
        });
    })
    .catch(error => console.error('Error fetching voices:', error));
}

async function sendTextToTTS() {
  await Word.run(async (context) => {
      const range = context.document.getSelection();
      range.load("text");
      await context.sync();

      const selectedText = range.text; // Or use a hardcoded string for testing
      // Retrieve the selected voice and language from the <select> elements
      const voiceSelectElement = document.getElementById('voice');
      const voiceSelected = voiceSelectElement.value;

      const languageSelectElement = document.getElementById('language');
      const languageSelected = languageSelectElement.value;

      // Get the user-input IP address and port number
      const ipAddress = document.getElementById('serverIP').value;
      const portNumber = document.getElementById('portNumber').value;

      // Example hardcoded values. Replace with actual values or dynamic ones
      //const voiceSelected = "Aerith_original.wav"; // Replace with dynamic or user-selected voice
      //const languageSelected = "en"; // Replace with dynamic or user-selected language
      const outputFileName = "demo_output.wav"; // Replace or generate dynamically as needed
      const streaming = "true"; // This can be dynamic based on user selection

      const audioPlayer = document.getElementById('audioSource');

      // Construct the query parameters string without URL encoding
      const queryParams = `text=${selectedText}&voice=${voiceSelected}&language=${languageSelected}&output_file=${outputFileName}&streaming=${streaming}`;

      // Set the audio source to the TTS server endpoint with query params
      //audioPlayer.src = `http://127.0.0.1:7851/tts-demo-request?${queryParams}`;
      audioPlayer.src = `http://${ipAddress}:${portNumber}/tts-demo-request?${queryParams}`;
      audioPlayer.load(); // Reload the audio player
  }).catch(error => {
      console.error("Error:", error);
  });
}
